    <el-row type="flex" class="row-bg" justify="center">
      <span>请扫码支付</span>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <img :src="img_scan_qr_guide" alt="img_scan_qr_guide">
      <img :src="img_scan_qr_code" alt="scan_qr_code">
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <span>请扫描二维码</span>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
      </el-col>
      <el-col :span="8">
        <router-link :to="{name: 'payment'}">
          <el-button type="primary" class="btn_large">返回</el-button>
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import img_scan_qr_guide from '@/assets/payments/payment_alipay_guide.png'
import img_scan_qr_code from '@/assets/hints/scan_qr_code.gif'

export default {
  data() {
    return {
      img_scan_qr_guide,
      img_scan_qr_code
    };
  }
}
</script>

<style scoped>
</style>



